body {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat; }

.container {
  width: 95%;
  max-width: 40rem;
  margin: 0 auto;
  background: #FFFFFF;
  border-radius: .9rem;
  text-align: center;
  padding: 2rem 2.5rem 0 2.5rem; }
  .container header {
    font-size: 2.8rem;
    font-weight: 600;
    margin: 0 0 -3.5rem 0; }
  .container .form-principal {
    width: 100%;
    overflow: hidden; }
  .container form {
    display: flex;
    width: 400%; }
    .container form .pagina {
      width: 25%;
      transition: margin-left .3s ease-in-out; }
      .container form .pagina .titulo {
        text-align: left;
        font-size: 20px;
        font-weight: 500; }
      .container form .pagina .campo {
        height: 10%;
        width: 100%;
        margin: 40px 0;
        display: flex;
        position: relative; }

.form-principal form .pagina .campo .label {
  position: absolute;
  top: -30px;
  font-weight: 500;
  font-size: 14px;
  font-style: italic;
  float: left;
  text-align: left;
  width: 100%; }

.form-principal .pagina .campo input {
  width: 100%;
  height: 100%;
  border: 1px solid lightgray;
  border-radius: 5px;
  font-size: 16px;
  padding-left: 15px; }

.form-principal form .pagina .campo select {
  width: 100%;
  padding-left: 10px;
  font-size: 17px;
  font-weight: 500;
  border-radius: 5px;
  border: 1px solid lightgray; }

.container .btn-cancelar {
  width: 70px;
  height: 70px;
  padding: 15px 15px;
  background: #db2a31;
  color: #FFFFFF;
  border-radius: 18%;
  position: relative;
  font-size: 15px;
  text-decoration: none;
  top: auto;
  right: auto; }
  @media (min-width: 768px) {
    .container .btn-cancelar {
      top: -1.1rem;
      right: -17.6rem; } }

.form-principal form .pagina .campo button {
  width: 100%;
  height: calc(100% + 5px);
  margin-top: -20px;
  margin-bottom: 4rem;
  border: none;
  background: #0e2c48;
  border-radius: 7px;
  color: #FFFFFF;
  font-size: 18px;
  font-weight: 500;
  letter-spacing: 1px;
  text-transform: uppercase; }

.form-principal form .pagina .campo button:hover {
  cursor: pointer;
  transition: .3s ease;
  background: #db2a31; }

.form-principal form .pagina .btns button {
  margin-top: -20px !important; }

form .pagina .btns button.volver {
  margin-right: 3px;
  font-size: 17px; }

form .pagina .btns button.adelante {
  margin-left: 3px; }

.ojitos {
  position: absolute;
  display: flex;
  top: 25%;
  right: 7px;
  font-size: 20px;
  color: #0e2c48; }

#ocultar {
  display: none; }

#mostrar, #ocultar {
  cursor: pointer; }

.error {
  color: #db2a31;
  float: right;
  text-align: right; }

.container .progress-bar {
  display: flex;
  margin: 40px 0; }
  .container .progress-bar .paso {
    text-align: center;
    width: 100%;
    position: relative; }
    .container .progress-bar .paso p {
      font-size: 18px;
      font-weight: 500;
      color: #000;
      margin-bottom: 8px;
      transition: .2s;
      display: none; }
      @media (min-width: 768px) {
        .container .progress-bar .paso p {
          display: block; } }
      @media (min-width: 480px) {
        .container .progress-bar .paso p {
          display: block; } }
      .container .progress-bar .paso p.active {
        color: #db2a31; }
    .container .progress-bar .paso .num {
      position: relative;
      width: 3rem;
      height: 3rem;
      border: 2px solid #000000;
      border-radius: 50%;
      display: inline-block;
      font-weight: 500;
      font-size: 17px;
      line-height: 25px;
      transition: .2s; }
      .container .progress-bar .paso .num.active {
        border-color: #db2a31;
        background: #db2a31; }
        .container .progress-bar .paso .num.active span {
          display: none; }
      .container .progress-bar .paso .num span {
        font-weight: 500;
        font-size: 17px;
        line-height: 25px;
        position: absolute;
        left: 50%;
        transform: translateX(-50%); }
    .container .progress-bar .paso .check {
      display: none;
      position: absolute;
      left: 50%;
      top: 75%;
      font-size: 15px;
      transform: translate(-50%, -50%); }
      @media (min-width: 203px) {
        .container .progress-bar .paso .check {
          transform: translate(-50%, -130%); } }
      @media (min-width: 320px) {
        .container .progress-bar .paso .check {
          transform: translate(-50%, -130%); } }
      @media (min-width: 480px) {
        .container .progress-bar .paso .check {
          transform: translate(-50%, -50%); } }
      .container .progress-bar .paso .check.active {
        display: block;
        color: #FFFFFF; }

.progress-bar .paso .num::before,
.progress-bar .paso .num::after {
  position: absolute;
  content: '';
  width: 0px;
  height: 0px;
  bottom: 0px;
  right: 0px; }
  @media (min-width: 768px) {
    .progress-bar .paso .num::before,
    .progress-bar .paso .num::after {
      width: 44px;
      height: 3px;
      bottom: 11px;
      right: -53px;
      background: #000000; } }
  @media (min-width: 480px) {
    .progress-bar .paso .num::before,
    .progress-bar .paso .num::after {
      width: 44px;
      height: 3px;
      bottom: 11px;
      right: -53px;
      background: #000000; } }

.progress-bar .paso:last-child .num::before,
.progress-bar .paso:last-child .num::after {
  display: none; }

.progress-bar .paso .num.active::after {
  background: #db2a31;
  animation: mov_Linea 0.3s linear forwards;
  transform: scaleX(0);
  transform-origin: left; }

@keyframes mov_Linea {
  100% {
    transform: scaleX(1); } }

.alerta-span {
  font-size: 1.6rem; }

.alerta-ul {
